.line {
    stroke-dasharray: 12;
    stroke-dashoffset: 3;
    stroke-linecap: round;
    animation: lineMotion infinite 60s linear;

}

@keyframes lineMotion {
    0% {
        stroke-dasharray: 12;
        stroke-dashoffset: 2;
    }

    35% {
        stroke-dasharray: 8 10;
    }

    75% {
        stroke-dasharray: 2 10;
    }

    100% {
        stroke-dasharray: 12;
        stroke-dashoffset: 2000;
    }
}

/* Default styles for larger screens */
.responsive-svg {
    width: 1200px;
    height: 880px;
}

@media only screen and (max-width: 600px) {
    .responsive-svg {
        width: 600px;
        height: 600px;
        margin-top: 70px;
        transform: rotate(-100deg) scale(1.2) translateX(15px) translateY(30px);
    }
}

@media only screen and (min-width: 700px) and (max-width: 800px) {
    .responsive-svg {
        width: 700px;
        height: 700px;
        margin-top: 70px;
        transform: rotate(-100deg) scale(1.2) translateX(15px) translateY(30px);
        
    }
}
@media only screen and (min-width: 1900px) and (max-width: 2000px) {
    .responsive-svg {
        width: 1900px;
        height: 1200px;
        margin-top: 70px;
        transform: rotate(-100deg) scale(1.2) translateX(15px) translateY(30px);
    }
}
