.thankYouContainer {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100vw;
  height: 100vh;
  text-align: center;
  margin: 0;
  box-sizing: border-box;
  flex-direction: row;
  background: linear-gradient(180deg, #fff 50.88%, #e8f0ff 100%);
  position: relative;
  overflow: hidden;
}

.boxImage {
  width: 200px;
  height: 200px;
  position: absolute;
  bottom: 0;
  left: -25px;
  margin: 0;
  margin-bottom: 0;
}

.messageContainer,
.demoContainer {
  flex: 1;
  margin: 0 20px;
}

.demoContainer {
  color: #1e38af;
  margin-right: 80px;
}

.thankYouImage {
  width: 80px;
  height: 80px;
}

.text {
  color: #3e59bf;
  font-size: 3em;
  font-weight: 700;
  margin: 10px 0;
}

.desc {
  color: #888888;
  font-size: 1.2em;
  font-weight: 400;
}

.demoText {
  color: #1e38af;
  margin-bottom: 20px;
  font-weight: 600;
}

.mockUpWrapper {
  position: relative;
  display: inline-block;
}

.demoImage {
  max-width: 80%;
  height: auto;
}

.demoVideo {
  position: absolute;
  top: 10px;
  right: 67px;
  height: 63%;
}

