@keyframes fadeInOut {
  0% {
    opacity: 1;
  }

  45% {
    opacity: 0;
  }

  55% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.image-container {
  position: relative;
  width: 100%;
  min-height: 130vh;
  /* Default for large screens */
}

.image-container img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 135vh;
  opacity: 0;
  animation: fadeInOut 10s ease-in-out infinite;
}

.image-container img:nth-child(1) {
  animation-delay: 0s;
}

.image-container img:nth-child(2) {
  animation-delay: 4s;
}

.image-container img:nth-child(3) {
  animation-delay: 8s;
}

.image-container img:nth-child(4) {
  animation-delay: 16s;
}

@media only screen and (max-width: 600px) {
  .image-container {
    min-height: 50vh;
  }

  .image-container img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 50vh;
    opacity: 0;
    animation: fadeInOut 10s ease-in-out infinite;
  }


}


@media only screen and (min-width: 600px) and (max-width: 768px) {
  .image-container {
    min-height: 60vh;
  }

  .image-container img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 60vh;
    opacity: 0;
    animation: fadeInOut 16s ease-in-out infinite;
  }

  .top-paragraph {
    font-size: 32px;

  }
}

@media only screen and (min-width: 768px) and (max-width: 992px) {
  .image-container {
    min-height: 80vh;
  }

  .image-container img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 80vh;
    opacity: 0;
    animation: fadeInOut 16s ease-in-out infinite;
  }
}


@media only screen and (min-width: 992px) and (max-width: 1200px) {
  .image-container {
    min-height: 130vh;
  }

  .image-container img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 130vh;
    opacity: 0;
    animation: fadeInOut 16s ease-in-out infinite;
  }
}


@media only screen and (min-width: 1200px) {
  .image-container {
