}

@keyframes gradientChange {
  0% {
    background: linear-gradient(88.58deg, #FFBFDA -2.79%, #9D50FF 66.67%, #ACAAFF 98.91%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }

  25% {
    background: linear-gradient(88.58deg, #FFBFDA -2.79%, #9D50FF 98.91%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }

  50% {
    background: linear-gradient(88.58deg, #ACAAFF -2.79%, #FFBFDA 98.91%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }

  75% {
    background: linear-gradient(88.58deg, #ACAAFF -2.79%, #9D50FF 66.67%, #FFBFDA 98.91%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }

  100% {
    background: linear-gradient(88.58deg, #FFBFDA -2.79%, #9D50FF 66.67%, #ACAAFF 98.91%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }
}

.gradient-text {
  display: inline-block;
  background: linear-gradient(88.58deg, #ACAAFF -2.79%, #9D50FF 66.67%, #ACAAFF 98.91%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: gradientChange 20s ease-in-out infinite;
}






