@keyframes fadeInFromBottom {
  from {
    opacity: 0;
    transform: translateY(40px) scale(0.65);
    visibility: hidden; /* Start hidden */
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(0.65);
    visibility: visible; /* End visible */
  }
}

@keyframes fadeInFromTop {
  from {
    opacity: 0;
    transform: translateY(-40px) scale(0.65);
    visibility: hidden; /* Start hidden */
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(0.65);
    visibility: visible; /* End visible */
  }
}


.fadeInFromBottom {
  animation: fadeInFromBottom ease-out 400ms forwards; /* Use forwards to maintain the end state */
}

.fadeInFromTop {
  animation: fadeInFromTop ease-out 400ms forwards; /* Use forwards to maintain the end state */
}

