/* Add this to your CSS file */
.backdrop-blur {
    backdrop-filter: blur(25px); /* Adjust blur strength as needed */
    background-color: rgba(255, 255, 255, 0.5); /* Adjust opacity as needed */
  }
  .shadow-top {
    box-shadow: 0 -2px 0px 0px rgba(248, 247, 247, 0.6), 0 0px 0px 0px rgba(0, 0, 0, 0.06);
  }
  