@media (max-width: 768px) {
  .heading {
    font-size: 1.5rem;
    margin-bottom: 20px;
    margin-top: 20px;
  }

  .imageGrid {
    grid-template-columns: 1fr;
    gap: 20px;
    padding: 20px 0 20px 0;
  }

  .card1,
  .card2,
  .card3 {
    grid-column: span 1;
    margin-top: 0;
  }

  .card1 .image {
    border-radius: 35px 35px 0 0;
  }

  .card2 {
    display: flex;
    flex-direction: column;
  }

  .card2 .textCard {
    order: 2;
    border-radius: 0 0 35px 35px;
  }

  .card2 .image {
    order: 1;
    border-radius: 35px 35px 0 0;
  }

  .card3 .image {
    border-radius: 35px 35px 0 0;
  }
}
